import Vue from 'vue'
import VueRouter from 'vue-router'
import About from '@/views/About'
import Board from '@/views/Board'
import Calender from '@/views/Calender'

Vue.use(VueRouter)

const routes = [
    {
        path: '/',
        name: 'About',
        component: About,
    },
    {
        path: '/board',
        name: 'Board',
        component: Board,
    },
    {
        path: '/calender',
        name: 'Calender',
        component: Calender,
    },
]

const router = new VueRouter({
    mode: 'history',
    routes
})

export default router